/**
 * Main App - Provide music equalizer functionaities
 */
const $ = React.createElement;
import Range from './range.js';
import Frequency from './frequency.js';
import Preset from './preset.js';

const frequencyLables = ['60', '310', '1K', '6K', '16K'];
const arr5 = [...Array(5).keys()];

export default class App extends React.Component {
  constructor(props) {
        super(props);
        let val = localStorage.getItem('fband');
        if(val){
            val = JSON.parse(val);
        } else {
            val = (new Array(5)).fill(0)
        }
        this.state = {
            fband: val
        };
        this.btnClick = this.btnClick.bind(this);
        this.frequencyChange = this.frequencyChange.bind(this);
        this.onPreset = this.onPreset.bind(this);
    }

    btnClick(){
        this.setState({val: this.state.val+1});
    }

    frequencyChange(id, curValue) {
        let fband = this.state.fband.slice();
        fband[id] = curValue;
        this.setState({fband});
        localStorage.setItem('fband', JSON.stringify(fband));
        
        //console.log('frequencyChange: ', curValue, key);
    }

    onPreset(fband) {
        this.setState({fband});
        localStorage.setItem('fband', JSON.stringify(fband));
    }

    render() { return $('div', {class: 'flex flex1 flexc'}, 
        $('header', {class: 'flex justsb alignc white bg-purple1'},
            $('span', {class: 'flex alignc'}, 'React Music Equalizer'),
            $('a', {class: '', href: 'https://github.com/kumarabhishek/kumarabhishek.github.io/tree/master/apps/equalizer', target: '_blank'},
                $('svg', {ariaHidden: true, class:'octicon octicon-mark-github', height:"32", viewBox:"0 0 16 16", width:"32"},
                    $('path', {fillRule:"evenodd", d:"M8 0C3.58 0 0 3.58 0 8c0 3.54 2.29 6.53 5.47 7.59.4.07.55-.17.55-.38 0-.19-.01-.82-.01-1.49-2.01.37-2.53-.49-2.69-.94-.09-.23-.48-.94-.82-1.13-.28-.15-.68-.52-.01-.53.63-.01 1.08.58 1.23.82.72 1.21 1.87.87 2.33.66.07-.52.28-.87.51-1.07-1.78-.2-3.64-.89-3.64-3.95 0-.87.31-1.59.82-2.15-.08-.2-.36-1.02.08-2.12 0 0 .67-.21 2.2.82.64-.18 1.32-.27 2-.27.68 0 1.36.09 2 .27 1.53-1.04 2.2-.82 2.2-.82.44 1.1.16 1.92.08 2.12.51.56.82 1.27.82 2.15 0 3.07-1.87 3.75-3.65 3.95.29.25.54.73.54 1.48 0 1.07-.01 1.93-.01 2.2 0 .21.15.46.55.38A8.013 8.013 0 0 0 16 8c0-4.42-3.58-8-8-8z"})
                )
            )
        ),
        $('div', {class: 'content flex flex1 flexc justc'},
            $('div', {class: 'flex justc pad1r'},
                $(Range, {}),
                $('div', {class: 'flex'},
                    [...Array(5).keys()].map((_, i) => $(Frequency,
                        {
                            key: i,
                            id: i,
                            onChange: this.frequencyChange,
                            value: this.state.fband[i],
                            min: -12, max: 12,
                            step: 1
                        }
                    ))
                ),
                $(Preset, {onPreset: this.onPreset})
            ),
            $('div', {class: 'flex'}, 
                arr5.map((_, i) =>
                    $('span', {class: 'freq-label'}, frequencyLables[i])
                )
            )
        ),
        $('footer', {},
            $('span', {}, 'Â© 2018 Kumar Abhishek, | '),
            $('a', {href: 'https://kumarabhishek.github.io', target: '_blank'}, ' Author |'),
            $('a', {href: 'mailto:kumarabhishek.opensource@gmail.com'}, ' Contact')
        )
    );
  }
};
